/*Hazparte*/

.bell a{
    text-decoration: none;
    color: var(--amarillo);
}
.bell{
    cursor: pointer;
    left: 87%;
    top: 51%;
    position: fixed;
    font-size: 50px;
    color: var(--amarillo);
}


@media screen and (min-width:1024px){
    .bell{
        left: 95%;
    }
}